import React, { useContext, useEffect } from "react";
import { FormInput } from "../Form/FormInput";
import { Context as DatesContext } from "../../context/DatesContext";

export const NavDays = () => {
  const {
    state: { calendar, searchName },

    addIntervalMsg,
    searchNameMsg,
  } = useContext(DatesContext);
  const { intervalData, intervalMonth, intervalRentedArray, startDate } =
    calendar;

  return (
    <div style={{ display: "flex", flexDirection: "column", width: "10%" }}>
      <div style={{}}>
        <FormInput
          handleChange={(newDate) => {
            addIntervalMsg("startDate", newDate);
          }}
          value={startDate}
          type="date"
          label="desde"
        />
      </div>
      <div style={{}}>
        <FormInput
          handleChange={(e) => searchNameMsg(e.target.value)}
          value={searchName}
          type="text"
          label="Nombre"
        />
      </div>
    </div>
  );
};
